import "./index.css"
import { OrderCart } from "../../components/OrderCart"
import { useEffect, useState } from "react"
import Axios from "../../api/Axios"
import trackUserEvent from "../../helpers/trackUserEvent";

const axios = new Axios();

const Orders = () => {

  const [userOrders, setUserOrders] = useState([])

  const getAllUserOrders = async () => {
    const options = {
      method: "GET"
    };
    try {
      const result = await axios.callApi(
        `/user/getAllOrders`,
        options
      );
      if (result.status == 200) {
        setUserOrders(result.data.data)
      }
    }
    catch (error) {
      console.error("Something went wrong")
    }
  }

  useEffect(() => {
    getAllUserOrders()
  }, [])

  useEffect(() => {
    trackUserEvent("My Orders")
  },[])

  return (
    <div className=".container-fluid" style={{ backgroundColor: "white" }}>
      {userOrders.length == 0 ? <span className="text-style order-msg">
        No order has been made yet.
      </span> : userOrders.map((e) => <OrderCart id={e.id} totalPrice={e.orderTotalAmount} orderStatus={e.orderStatus} orderDetails={e.orderDetails} date={e.placedAt} />)}
    </div>
  )
}

export default Orders
